import React from "react";

//creating react component
export default function ProjetDetail(props) {
  let emojis = ["🔥", "👀", "⭐", "💻", "🐧", "🐭", "👋", "🍕", "🎷", "🙌"];
  return (
    <div class="projectTile">
      <div class="projectHeader">
        <h1>{props.content.titre.toUpperCase()}</h1>
        <ul style={{ paddingLeft: "16px" }}>
          {props.content.skilz.map((skill) => {
            return <li>{skill}</li>;
          })}
        </ul>
      </div>
      <div className={(props.content.yr != "🙋🏻‍♂️") ? "projectDescription" : "presentationCont"}>
        {props.content.description.split("//").map((line, index) => {
          return (
            <p>
              {(props.content.yr != "🙋🏻‍♂️") ? <div class="titreLigne">{["Contexte", "Périmètre", "Faits clés"][index]}</div> : ""}
              {line.replaceAll('<br/>', '\n')}
            </p>
          );
        })}
        {props.content.link != "" ? (
          <a class="projLink" href={props.content.link} target="_blank" rel="noopener noreferrer">
            {emojis[Math.floor(Math.random() * emojis.length)]} Voir le projet
          </a>
        ) : (
          <div></div>
        )}
      </div>
    </div>
  );
}
