import React from "react";
import "/style/slider.css";
//creating react component
export default function Projets(props) {
  function handleClick(e) {
    props.stateSet(props.content[e.target.value]);
  }
  return (
    <div class="projsZone">
      <input type="range" min="0" max={props.content.length - 1} value={props.content.findIndex((x) => JSON.stringify(x) == JSON.stringify(props.selected))} onChange={handleClick} />
      <div class="ticks">
        {props.content.map((projet, index) => {
          return (
            <span id={index} class="tick">
              {projet.yr}
            </span>
          );
        })}
      </div>
    </div>
  );
}
